.TourDetailsWrapper{
    display: flex;
    flex-direction: column;
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
}

.TourDetailsWrapper h2{
    color: #102D69;
    font-family: "Arial";
    font-size: 22px;
    font-weight: bold;
}

.TourChoiceBlockWrapper{
    display: grid;
    grid-template-columns:repeat(2,50%);
    margin-top: 2vh;
}

.Icons{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    /* font-weight: bold; */
    color: #102D69;
    font-family: "Arial";
    font-size: 17px;
}

.TourDetailsInner{
    display: flex;
    flex-direction: row;
    align-items: space-between;
    width: 100%;
}

.TourDetailsInnerSmallScreen{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.GalleryTourDetails{
    /* max-width: 45%; */
    margin-right: 3vw;
}

.TourBookingDetails{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    /* min-width: 45%; */
    min-height: 60vh;
    border: 2px solid rgb(206, 207, 209);
    border-radius: 6px;
    margin-top: 2vw;
}

.TourBookingDetails h3{
    text-align: center;
    color: #005AEB;
    font-size: 22px;
    font-weight: bold;
    font-family: 'Arial Narrow';
}

